'use strict';

/**
* Return a list of ndarray data types.
*
* @module @stdlib/ndarray/dtypes
*
* @example
* import dtypes from '@stdlib/ndarray/dtypes' ;
*
* var list = dtypes();
* // returns [...]
*/

// MODULES //

import setReadOnly from '@stdlib/utils/define-nonenumerable-read-only-property' ;
import dtypes from './main.js' ;
import enumeration from './enum.js' ;
import assign from './assign.js' ;


// MAIN //

setReadOnly( dtypes, 'enum', enumeration );
assign( dtypes, enumeration() );


// EXPORTS //

export default dtypes;
