'use strict';

/**
* Identity function.
*
* @param {*} x - input value
* @returns {*} input value
*
* @example
* var v = identity( 3.14 );
* // returns 3.14
*/
function identity( x ) {
	return x;
}


// EXPORTS //

export default identity;
